.page-container{
    display: flex;
    flex-direction: column;
}

.intro-container{
    background-image: url('../assets/image.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 70%;
    height: 100vh;
    width: 100%;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    flex-direction: column;
}

.intro-contents{
    position: relative;
    top: 15%;
    width: 100%;
}

.logo-title{
    color:white;
    padding-bottom: 5%;
    font-size: 150px;
    letter-spacing: 19px;
    font-weight: 100;
    text-align: center;

}


.social-links-container{
    margin-bottom: 8%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}



.social-links-container{
    margin-bottom: 8%;
}

.twitter-button-container{
    width: 400px;
    height: 70px;
    padding-bottom: 1%;
}

.discord-button-container{
    width: 400px;
    height: 70px;
}

/* teams section */

.teams-container{
    width: 100%;
    height: 100%;
}

.teams-title{
    padding-top: 5%;
    text-align: center;
    font-size: 70px;
    letter-spacing: 10px;
    font-weight: 100;
    color: white;
}

.who-para{
    color: white;
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
    padding-top: 3%;
    max-width: 700px;
    justify-self: center;
    margin-left: auto;
    margin-right: auto;
}

.teams-container{
    width: 100%;
    height: 100%;
    background-color: black;
    padding-bottom: 10%;
    position: relative;

}

.team-pics-container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.team-pic{
    width: 100%;
    height: auto;
    border: 2px solid black;
    transform: scale(1.2);
}

.team-title{
    color:#C99242;

  
}

.team-pic-wrapper{
    width: 200px;
    height: 200px;
    overflow: hidden;
    border-radius: 50%;
    border: 1px solid #ffff ;
    margin-bottom: 4%;
    position: relative;
    cursor: pointer;
}


.team-pic-wrapper-wrapper{
    height: 200px;
    height: 200px;
}



.team-pic-wrapper:hover .team-pic{
    box-shadow: 0 1px 6px rgba(0, 0, 0, 1000);
    background-color: rgba(0,0,0, 0.4);
    z-index: 1900;
    filter: blur(10px);
} 

.team-pic-wrapper:hover .twitter-link{
    z-index: 100;
}


.team-pic-list{
    margin-top: 2%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}


.team-pic-container{
    margin: 2%;
    border-radius: 50%;
    border: 2px solid black;
    margin-top: 5%;
}

.team-title{
    letter-spacing: 7px;
    font-weight: 100;
    text-align: center;
    color: red;
}

.team-name{
    color: white;
    z-index: 1999;
    letter-spacing: 7px;
    font-weight: 100;
    text-align: center;
}

.fa-twitter:before {
    color: white;
    z-index: 100;
}

.team-info-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 300px;
}


.fa-twitter:before{
    font-size: 50px;
}
        


.team-title-container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.team-title-info{
    padding-top: 2%;
}

.team-para{
    color: rgb(199, 194, 194);
    font-weight: 100;
    font-family: 'Courier New', Courier, monospace;
    padding-top: 2%;
    text-align: center;
    max-width: 500px;
    font-size: 14px;
}

.twitter-wrapper{
    height: 100px;
    height: 100px;
    position: relative;
}


.twitter-link{
    position: absolute;
    z-index: -1 ;
    width: 100px;
    height: 100px;
    bottom: 10%;
    left: 40%;
}



.vcu-text{
    font-weight: 100;
    color: #F4BD41;
    font-family: 'Courier New', Courier, monospace;
    padding-top: 2%;
    text-align: center;
    font-weight: 900;
    max-width: 500px;
    font-size: 14px;
    text-decoration: underline;
} 

.vcu-link{
    font-weight: 100;
    color: #F4BD41;
    font-family: 'Courier New', Courier, monospace;
    padding-top: 2%;
    text-align: center;
    font-weight: 900;
    max-width: 500px;
    font-size: 14px;
    text-decoration: underline;
}


/* ROADMAP */

.road-conatiner{
    position: relative;
    overflow: hidden;
}

.road-conatiner:hover{
    content: " ";
    height: 100%;
    width: 100%;
}


.road-conatiner .bg{
    position: fixed;
    background: url(../assets/roadbg.jpg);
    background-size: cover;
    height: 100%;
    z-index: -1;
    top: 0;
    width: 100vw;

}

.road-conatiner .blur{
    background: rgba(0,0,0,.2);
    -webkit-backdrop-filter: blur(1px);
    backdrop-filter: blur(1px);
    height: 0;
    position: absolute;
    top: 0;
    width: 100%;
}

.roadmap{
    width: 100%; 
    height: 80%;
}

.roadmap-container{
    margin: 0 auto;
    width: 95%;
    position: relative;
    height: 100%;
}

.roadmap-img-wrapper{
    padding-top: 5%;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 10%;
}

.roadmap-img{
    width: 80%;
    height: auto;
}

.roadmap-title{
    position: absolute;
    font-family: 'Courier New', Courier, monospace;
    color: red;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    top: 16%;
    left: 0;
    right: 32%;
    text-align: center;
    font-size: 35px;
}


.title{
    display: block;
    font-family: 'Courier New', Courier, monospace;
