/* lore container */

.lore-container{
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 5%;
}


.lore-overview{
    color: white;
    font-family: 'Courier New', Courier, monospace;
    max-width: 800px;
    width: 100%;
    text-align: center;
    padding-bottom: 5%;
    padding-top: 5%;
}


.manga-section-container{
    padding-top: 10%;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 5%;
}

.manga-img-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 1180px;
    position: relative;
    overflow: hidden;
}

.merch-img-container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    max-width: 1180px;
    position: relative;
    overflow: hidden;
}


/* FAQ SECTION */


.faq-bg{
    display: flex;
